  padding-left: calc(var(--bs-gutter-x) * 0.5);
  margin-top: var(--bs-gutter-y);
  flex: 0 0 auto;
  width: 100%;
  padding-top: 1rem;
  padding-bottom: 1rem;
}
@media (min-width: 768px) {
  .decidable-main {
    flex: 0 0 auto;
    width: 80%;
  }
}
@media (min-width: 992px) {
  .decidable-main {
    flex: 0 0 auto;
    width: 83.33333333%;
  }
}
@media (min-width: 1200px) {
  .decidable-main {
    flex: 0 0 auto;
    width: 75%;
    border-right: 1px solid #e6e6e6;
  }
}
@media (min-width: 1590px) {
  .decidable-main {
    flex: 0 0 auto;
    width: 66.66666667%;
    border-right: 1px solid #e6e6e6;
  }
}

.decidable-content {
  padding-bottom: 1rem;
}
.decidable-content h2, .decidable-content .h2 {
  padding-top: 1rem;
}
.decidable-content code {
  padding: 0.0625rem 0.25rem;
  font-family: "Source Code Pro", monospace;
  font-size: 1rem;
  font-style: normal;
  font-weight: 600;
  white-space: nowrap;
  background-color: #e6e6e6;
  border-radius: 0.25rem;
}
.decidable-content .csl-entry {
  padding-left: 2rem;
  text-indent: -2rem;
}

.decidable-legend dt {
  display: inline-block;
  width: 12rem;
  padding: 0.25rem 0.675rem;
  font-weight: 600;
}
.decidable-legend dd {
  margin-top: 0.25rem;
  margin-bottom: 1.5rem;
  margin-left: 2rem;
}
.decidable-legend .x {
  background: #f28d8e;
  border-left: 1rem solid #e41a1c;
}
.decidable-legend .a {
  background: #ffbf80;
  border-left: 1rem solid #ff7f00;
}
.decidable-legend .l {
  background: #ffff99;
  border-left: 1rem solid #ffff33;
}
.decidable-legend .v {
  background: #fbc0df;
  border-left: 1rem solid #f781bf;
}
.decidable-legend .p {
  background: #9bbfdc;
  border-left: 1rem solid #377eb8;
}
.decidable-legend .g {
  background: #a6d7a5;
  border-left: 1rem solid #4daf4a;
}
.decidable-legend .w {
  background: #cca7d1;
  border-left: 1rem solid #984ea3;
}
.decidable-legend .u {
  background: #d3ab94;
  border-left: 1rem solid #a65628;
}
.decidable-legend .chosen {
  background: #cccccc;
  border-left: 1rem solid #999999;
}
.decidable-legend .better {
  background: #a2a2e8;
  border-left: 1rem solid #4545d0;
}
.decidable-legend .worse {
  background: #f899f3;
  border-left: 1rem solid #f032e6;
}
.decidable-legend .even {
  background: #88ede4;
  border-left: 1rem solid #10dbc9;
}
.decidable-legend .correct {
  padding-left: calc(1.675rem - 1px);
  background: #ffffff;
  border: 1px solid #4d4d4d;
}
.decidable-legend .error {
  color: #ffffff;
  background: gray;
  border-left: 1rem solid #000000;
}

.decidable-glossary {
  margin-left: 2rem;
}
.decidable-glossary dt {
  display: inline;
  margin-left: -2rem;
  font-weight: 600;
}
.decidable-glossary dt::after {
  content: ":";
}
.decidable-glossary dd {
  display: inline;
}
.decidable-glossary dd::after {
  display: block;
  content: "";
}

.decidable-footer {
  --bs-gutter-x: 2rem;
  --bs-gutter-y: 0;
  display: flex;
  flex-wrap: wrap;
  margin-top: calc(-1 * var(--bs-gutter-y));
  margin-right: calc(-0.5 * var(--bs-gutter-x));
  margin-left: calc(-0.5 * var(--bs-gutter-x));
  padding-top: 1rem;
  border-top: 1px solid #e6e6e6;
}

.decidable-footer-item {
  flex-shrink: 0;
  width: 100%;
  max-width: 100%;
  padding-right: calc(var(--bs-gutter-x) * 0.5);
  padding-left: calc(var(--bs-gutter-x) * 0.5);
  margin-top: var(--bs-gutter-y);
  flex: 0 0 auto;
  width: auto;
  max-width: none;
  padding: 0 0.5rem;
  font-size: 1rem;
  font-weight: 600;
  line-height: 1.1;
}
.decidable-footer-item.decidable-footer-item-left {
  margin-right: auto;
}

